    color: #011c40;
  }
}

@media (max-width: 767px) {
  .is-active {
    background-color: #f9fbfd;
    padding-left: 25px;
    padding-right: 8px;
    flex-direction: column-reverse;
    justify-content: flex-end;
    width: 89%;
