    font-size: 16px;
    line-height: 140%;
    width: 90%;
    margin-bottom: 32px;
  }
}

.form__inputs .input-message {
  position: absolute;
  color: #f9fbfd;
  left: 0;
  display: block;
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;
  font-family: "Montserrat", "Arial", sans-serif;
}

.form__checkbox .input-message {
  position: absolute;
  top: 100%;
}

